/* eslint-disable @typescript-eslint/no-misused-promises */
"use client"

import { FormEvent, useState } from "react"
import { Mail, MapPin, Phone, Clock } from "lucide-react"
import { BreadcrumbHero } from "@/components/breadcrumb-hero"

export default function ContactPage() {
  const [status, setStatus] = useState<"idle" | "loading" | "success" | "error">("idle")
  const [error, setError] = useState<string | null>(null)

  const handleSubmit = async (event: FormEvent<HTMLFormElement>) => {
    event.preventDefault()
    setStatus("loading")
    setError(null)

    const formData = new FormData(event.currentTarget)
    const payload = {
      firstName: formData.get("firstName")?.toString().trim(),
      lastName: formData.get("lastName")?.toString().trim(),
      email: formData.get("email")?.toString().trim(),
      phone: formData.get("phone")?.toString().trim(),
      message: formData.get("message")?.toString().trim(),
    }

    if (!payload.firstName || !payload.lastName || !payload.email || !payload.message) {
      setError("Please fill in all required fields.")
      setStatus("error")
      return
    }

    try {
      const response = await fetch("/api/contact", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(payload),
      })

      if (!response.ok) {
        const data = await response.json().catch(() => ({}))
        throw new Error(data.error || "Something went wrong")
      }

      setStatus("success")
      event.currentTarget.reset()
    } catch (err) {
      console.error(err)
      setStatus("error")
      setError("We couldn't send your message. Please try again later.")
    }
  }

  return (
    <div className="pt-36 min-h-screen font-sans">
      <BreadcrumbHero
        title="Contact Us"
        subtitle="Ready to discuss your business needs? We're here to help you bridge the gap to capital."
        breadcrumbs={[{ label: "Home", href: "/" }, { label: "Contact" }]}
      />

      {/* Contact Content */}
      <section className="py-24 bg-white">
        <div className="container mx-auto px-6">
          <div className="grid lg:grid-cols-2 gap-16">
            {/* Contact Info */}
            <div>
              <h2 className="text-3xl md:text-4xl font-sans font-bold mb-8 text-foreground">
                Let's Start a Conversation
              </h2>
              <p className="text-lg text-muted-foreground mb-12 leading-relaxed font-sans">
                Whether you're looking to raise capital, restructure your business, or explore strategic opportunities,
                our team is ready to assist you.
              </p>

              <div className="space-y-6">
                <div className="flex items-start gap-6 p-6 bg-gradient-to-br from-blue-50 to-white rounded-xl border border-border hover:shadow-md transition-shadow">
                  <div className="w-12 h-12 bg-primary/10 rounded-xl flex items-center justify-center shrink-0">
                    <MapPin className="text-primary" size={24} />
                  </div>
                  <div>
                    <h3 className="font-sans text-lg font-bold mb-2 text-foreground">Visit Our Office</h3>
                    <p className="text-muted-foreground leading-relaxed font-sans">
                      5th Floor, Bloukrans Building
                      <br />
                      Lynnwood Bridge, Pretoria, 0081
                      <br />
                      South Africa
                    </p>
                  </div>
                </div>

                <div className="flex items-start gap-6 p-6 bg-gradient-to-br from-teal-50 to-white rounded-xl border border-border hover:shadow-md transition-shadow">
                  <div className="w-12 h-12 bg-accent/10 rounded-xl flex items-center justify-center shrink-0">
                    <Phone className="text-accent" size={24} />
                  </div>
                  <div>
                    <h3 className="font-sans text-lg font-bold mb-2 text-foreground">Call Us</h3>
                    <p className="text-muted-foreground font-sans">
                      <a href="tel:+27121400915" className="hover:text-accent transition-colors">
                        +27 12 140 0915
                      </a>
                      <br />
                      <a href="tel:+27798891233" className="hover:text-accent transition-colors">
                        +27 79 889 1233
                      </a>
                    </p>
                  </div>
                </div>

                <div className="flex items-start gap-6 p-6 bg-gradient-to-br from-blue-50 to-white rounded-xl border border-border hover:shadow-md transition-shadow">
                  <div className="w-12 h-12 bg-primary/10 rounded-xl flex items-center justify-center shrink-0">
                    <Mail className="text-primary" size={24} />
                  </div>
                  <div>
                    <h3 className="font-sans text-lg font-bold mb-2 text-foreground">Email Us</h3>
                    <p className="text-muted-foreground font-sans">
                      <a href="mailto:bodibe@kwetjiwe.co.za" className="hover:text-primary transition-colors">
                        bodibe@kwetjiwe.co.za
                      </a>
                      <br />
                      <a href="mailto:bodibe@bodibebathaba.co.za" className="hover:text-primary transition-colors">
                        bodibe@bodibebathaba.co.za
                      </a>
                    </p>
                  </div>
                </div>

                <div className="flex items-start gap-6 p-6 bg-gradient-to-br from-teal-50 to-white rounded-xl border border-border hover:shadow-md transition-shadow">
                  <div className="w-12 h-12 bg-accent/10 rounded-xl flex items-center justify-center shrink-0">
                    <Clock className="text-accent" size={24} />
                  </div>
                  <div>
                    <h3 className="font-sans text-lg font-bold mb-2 text-foreground">Business Hours</h3>
                    <p className="text-muted-foreground font-sans">
                      Monday - Friday: 8:00 AM - 5:00 PM
                      <br />
                      Saturday - Sunday: Closed
                    </p>
                  </div>
                </div>
              </div>
            </div>

            {/* Contact Form */}
            <div className="bg-gradient-to-br from-primary to-accent p-10 rounded-2xl shadow-2xl">
              <div className="bg-white/10 backdrop-blur-sm p-8 rounded-xl">
                <h3 className="text-2xl font-sans font-bold text-white mb-6">Send Us a Message</h3>
                <form className="space-y-6" onSubmit={handleSubmit}>
                  <div className="grid grid-cols-1 sm:grid-cols-2 gap-6">
                    <div className="space-y-2">
                      <label htmlFor="first-name" className="text-sm font-medium font-sans text-white/90">
                        First Name *
                      </label>
                      <input
                        id="first-name"
                        name="firstName"
                        type="text"
                        required
                        className="w-full px-4 py-3 bg-white/90 backdrop-blur-sm border border-white/20 rounded-lg focus:outline-none focus:ring-2 focus:ring-white transition-all text-foreground placeholder:text-muted-foreground font-sans"
                        placeholder="John"
                      />
                    </div>
                    <div className="space-y-2">
                      <label htmlFor="last-name" className="text-sm font-medium font-sans text-white/90">
                        Last Name *
                      </label>
                      <input
                        id="last-name"
                        name="lastName"
                        type="text"
                        required
                        className="w-full px-4 py-3 bg-white/90 backdrop-blur-sm border border-white/20 rounded-lg focus:outline-none focus:ring-2 focus:ring-white transition-all text-foreground placeholder:text-muted-foreground font-sans"
                        placeholder="Doe"
                      />
                    </div>
                  </div>

                  <div className="space-y-2">
                    <label htmlFor="email" className="text-sm font-medium font-sans text-white/90">
                      Email *
                    </label>
                    <input
                      id="email"
                      name="email"
                      type="email"
                      required
                      className="w-full px-4 py-3 bg-white/90 backdrop-blur-sm border border-white/20 rounded-lg focus:outline-none focus:ring-2 focus:ring-white transition-all text-foreground placeholder:text-muted-foreground font-sans"
                      placeholder="john@company.com"
                    />
                  </div>

                  <div className="space-y-2">
                    <label htmlFor="phone" className="text-sm font-medium font-sans text-white/90">
                      Phone
                    </label>
                    <input
                      id="phone"
                      name="phone"
                      type="tel"
                      className="w-full px-4 py-3 bg-white/90 backdrop-blur-sm border border-white/20 rounded-lg focus:outline-none focus:ring-2 focus:ring-white transition-all resize-none text-foreground placeholder:text-muted-foreground font-sans"
                      placeholder="+27 ..."
                    />
                  </div>

                  <div className="space-y-2">
                    <label htmlFor="message" className="text-sm font-medium font-sans text-white/90">
                      Message *
                    </label>
                    <textarea
                      id="message"
                      name="message"
                      rows={5}
                      required
                      className="w-full px-4 py-3 bg-white/90 backdrop-blur-sm border border-white/20 rounded-lg focus:outline-none focus:ring-2 focus:ring-white transition-all resize-none text-foreground placeholder:text-muted-foreground font-sans"
                      placeholder="Tell us about your project and how we can help..."
                    />
                  </div>

                  <button
                    type="submit"
                    disabled={status === "loading"}
                    className="w-full py-4 bg-white text-primary font-bold font-sans rounded-lg hover:bg-white/90 transition-all shadow-lg hover:shadow-xl text-lg disabled:opacity-80 disabled:cursor-not-allowed"
                  >
                    {status === "loading" ? "Sending..." : "Send Message"}
                  </button>

                  <div className="space-y-2">
                    <p className="text-xs text-white/70 text-center font-sans">
                      * Required fields. We'll respond within 24 business hours.
                    </p>
                    {status === "success" && (
                      <p className="text-sm text-green-100 text-center font-sans">Thanks! Your message has been sent.</p>
                    )}
                    {status === "error" && error && (
                      <p className="text-sm text-red-100 text-center font-sans">{error}</p>
                    )}
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </section>
    </div>
  )
}
